export const orderStatus = (status: string) => {
    if (status === 'done') {
      return '';
    } else if (status === 'created') {
      return '';
    } else if (status === 'pending') {
      return '';
    } else {
      return '';
    }
  };
  
  export const statusStyles = (status: string) => {
    if (status === 'done') {
      return { color: '#00CCCC' };
    } else if (status === 'cancel') {
      return { color: '#F80000' };;
    } else {
      return { color: '#F2F2F3' };
    }
  };